.breadcrumb {
    margin-top: 20px;
    margin-bottom: 20px;
    margin-#{$start}: 0;
    display: flex;
    align-items: center;
    width: 100%;
    background: transparent;
    padding: 0;
    margin-bottom: 30px;
    @include breakpoint(large){
        margin-top: 0px;
        margin-bottom: 30px;
    }
    >span:nth-child(1),
    >span:nth-child(2){
        display: none;
        font-size: 0;
    }
    >span {
        a {
            color: #404040;
            font-size: 14px;
            display: inline-block;
            text-decoration: none;
            @include breakpoint(isMobile) {
                font-size: 12px;
               }
        }
       
        &:nth-child(2n)::before {
            display: inline-flex;
            margin: 0 5px;
            color: #5c5c5c;
            font-size: 12px;
            content: "|";
        }
        &:nth-child(2n) {
            font-size: 0;
        }
    }

   
 
    .current {
         color:  #9e9e9e;
         font-weight: 400;
         font-size: 14px;
         opacity: 1;
         line-height: 2;
         @include breakpoint(isMobile){
            line-height: unset;
         }
    }
} 

